.all {
  display: grid;
  width: 100%;
  height: 100vh;

  grid-template-rows: 60px auto;
  grid-row-gap: 0px;
  overflow: hidden;

}

.message-list {
  display: grid;
  width: 100%;
  height: 100vh;

  grid-template-columns: 50% 50%;
  grid-column-gap: 5px;

}

.score{
 background-color: #3a7eea;
  width: 150px;
  padding: 10px;
  color:white;
  border: 1px solid #ccc;
  border-radius: 15px;
      display: flex;
    clear: both; 
    justify-content: center; 
}

.score-all{
      display: flex;
    clear: both; 
}

.left-container {
  margin-top: 60px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.message-list-container {
  padding: 10px;
  padding-bottom: 70px;
  position: relative;
}

.response-list-container {
  background: #007aff;
  position: absolute;
  bottom: 0;
  width: 100%; 
  width: 100vw;
}

.compose {
  width: 48%; 
  width: 48vw;
}

.input-container {
  position: absolute;
  right: 0;
  width: 50%;
  text-align: center;
  background: #f2f5f9;
}
