.left-container {
  margin-top: 60px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.message-list-container {
  padding: 10px;
  padding-bottom: 70px;
  position: relative;
}

.response-list-container {
  background: #007aff;
  position: absolute;
  bottom: 0;
  width: 100%; 
  width: 100vw;
}

.compose {
  width: 48%; 
  width: 48vw;
}

.input-container {
  position: absolute;
  right: 0;
  width: 50%;
  text-align: center;
  background: #f2f5f9;
}

.messenger {
  display: grid;
  width: 100%;
  height: 100vh;
  background: #eeeef1;

  grid-template-columns: auto;
  grid-template-rows: 60px auto 300px;
  grid-column-gap: 0px;
  grid-row-gap: 1px;
}

.container {
  padding: 10px;
}

.scrollable {
  position: relative;
  /*overflow-y: scroll;*/
  -webkit-overflow-scrolling: touch;
}

.sidebar {
  background: white;
  grid-row-start: 1;
  grid-row-end: span 3;
  padding-left: 10px;
  /*text-align: center;*/
}

.content {
  background: white;
  grid-row-start: 1;
  grid-row-end: span 3;
}

.footer {
  grid-column-start: 2;
  background: white;
}

.overlay {
  position: fixed; /* Sit on top of the page content */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0; 
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
  cursor: pointer; /* Add a pointer on hover */
}

.btninstrc {
  display: inline-block;
  background: #005ce6;
  color: #e3e3ea;
  font: inherit;
  border: 0;
  outline: 0;
  padding: 0;
  -webkit-transition: all 200ms ease-in;
  transition: all 200ms ease-in;
  cursor: pointer;
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, .1);
  border-radius: 2px;
  padding: 12px 12px; 
  margin: 1px 1px  auto;
}

    
.btninstrc:hover {
  background: darken($button-bg-color, 4%);
}

.btninstrc:active {
  background: $button-bg-color;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .2);
}


